import type { VeloConfig } from '@wix/cli-site-definitions';
import { loadVeloConfig, saveVeloConfig } from './files/velo.config.js';

export interface ProjectModel {
  projectFolder: string;
  metaSiteId: string;
  revision: string;
}

export async function loadProjectModel(
  projectFolder: string
): Promise<ProjectModel> {
  const config = await loadVeloConfig(projectFolder);
  return {
    projectFolder,
    metaSiteId: config.siteId,
    revision: config.uiVersion,
  };
}

export async function saveProjectModel(model: ProjectModel): Promise<void> {
  const config: VeloConfig = {
    siteId: model.metaSiteId,
    uiVersion: model.revision,
  };

  await saveVeloConfig(model.projectFolder, config);
}
