import { join } from 'node:path';
import type { VeloConfig } from '@wix/cli-site-definitions';
import { CONFIG_FILENAME } from '@wix/cli-core-definitions';
import { veloConfigSchema } from '@wix/cli-site-definitions';
import { readJson, writeJson } from '@wix/cli-fs';
import { CliError, CliErrorCode } from '@wix/cli-error';

export async function saveVeloConfig(
  projectFolder: string,
  config: VeloConfig
) {
  await writeJson(join(projectFolder, CONFIG_FILENAME), config, {
    spaces: 2,
  });
}

export async function loadVeloConfig(projectFolder: string) {
  const configFile = await readJson(join(projectFolder, CONFIG_FILENAME));

  try {
    return veloConfigSchema.parse(configFile);
  } catch (e) {
    throw new CliError({
      code: CliErrorCode.InvalidVeloConfigJson({
        configFilename: CONFIG_FILENAME,
        cause: e,
      }),
    });
  }
}
